
'use client';

import { useEffect, useState } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import Link from 'next/link';
import { getSessionUser, getUserProfile } from '@/lib/actions';
import type { User, Event } from '@/lib/types';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import UserAvatar from '@/components/user-avatar';
import EventCard from '@/components/event-card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Loader2, UserCircle, CalendarDays, Ticket, LogOut, Home, Info, LogIn } from 'lucide-react';
import { Separator } from '@/components/ui/separator';

interface ProfileData {
  user: User;
  joinedEvents: Event[];
}

export default function ProfilePage() {
  const [profileData, setProfileData] = useState<ProfileData | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isDebugView, setIsDebugView] = useState(false);
  const router = useRouter();
  const searchParams = useSearchParams();

  useEffect(() => {
    async function fetchProfile() {
      setIsLoading(true);
      setError(null);
      setIsDebugView(false);
      setProfileData(null); 

      const debugUserId = searchParams.get('debug_user_id');

      if (debugUserId) {
        setIsDebugView(true);
        try {
          const data = await getUserProfile(debugUserId);
          if (data) {
            setProfileData(data);
          } else {
            setError(`无法加载调试用户 ${debugUserId} 的个人资料。该用户可能不存在。`);
          }
        } catch (err) {
          console.error(`获取调试用户 ${debugUserId} 个人资料失败:`, err);
          setError(`加载调试用户 ${debugUserId} 个人资料时发生错误。`);
        }
      } else {
        const sessionUser = await getSessionUser();
        if (!sessionUser) {
          router.push('/login?redirect=/profile');
          setIsLoading(false); // Stop loading as we are redirecting
          return;
        }
        try {
          const data = await getUserProfile(sessionUser.id);
          if (data) {
            setProfileData(data);
          } else {
            setError('无法加载您的个人资料。');
          }
        } catch (err) {
          console.error('获取个人资料失败:', err);
          setError('加载个人资料时发生错误。');
        }
      }
      setIsLoading(false);
    }

    fetchProfile();
  }, [router, searchParams]);

  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-15rem)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-4 text-muted-foreground">正在加载您的个人资料...</p>
      </div>
    );
  }

  if (error) {
    return (
       <div className="container mx-auto px-4 py-8 max-w-4xl">
        <Alert variant="destructive" className="max-w-lg mx-auto my-10">
          <AlertTitle>错误</AlertTitle>
          <AlertDescription>{error}</AlertDescription>
          <Button onClick={() => router.push(isDebugView ? '/login' : '/')} variant="outline" className="mt-4">
            {isDebugView ? <LogIn className="mr-2 h-4 w-4" /> : <Home className="mr-2 h-4 w-4" />}
            {isDebugView ? '返回登录页' : '返回首页'}
          </Button>
        </Alert>
      </div>
    );
  }

  if (!profileData && !isLoading) { // Handles case where data is null after loading (e.g. user not found but no error thrown)
     return (
      <div className="container mx-auto px-4 py-8 max-w-4xl">
        <Alert className="max-w-lg mx-auto my-10">
          <AlertTitle>未找到个人资料</AlertTitle>
          <AlertDescription>我们找不到请求的个人资料数据。如果您是调试用户，请确保用户ID有效。否则，请尝试登录。</AlertDescription>
          <Button onClick={() => router.push(isDebugView ? '/login' : '/login?redirect=/profile')} variant="outline" className="mt-4">
            <LogIn className="mr-2 h-4 w-4" /> {isDebugView ? '返回登录页' : '前往登录'}
          </Button>
        </Alert>
      </div>
    );
  }
  
  if (!profileData) return null; // Should be covered by above, but as a safeguard

  const { user, joinedEvents } = profileData;

  return (
    <div className="container mx-auto px-4 py-8 max-w-4xl">
      {isDebugView && (
        <Alert variant="default" className="mb-6 border-yellow-500 bg-yellow-50 dark:bg-yellow-900/30 dark:border-yellow-700">
          <Info className="h-5 w-5 text-yellow-700 dark:text-yellow-300" />
          <AlertTitle className="text-yellow-800 dark:text-yellow-200">调试模式</AlertTitle>
          <AlertDescription className="text-yellow-700 dark:text-yellow-300">
            当前正在以调试模式查看用户 <strong>{user.username}</strong> 的个人资料。
            <Button variant="link" onClick={() => router.push('/profile')} className="p-0 h-auto ml-2 text-yellow-700 dark:text-yellow-300 hover:underline">
              清除调试视图并查看本人资料
            </Button>
          </AlertDescription>
        </Alert>
      )}

      <Card className="overflow-hidden shadow-xl">
        <CardHeader className="bg-gradient-to-br from-primary/20 to-secondary/10 p-8 text-center">
          <div className="relative w-32 h-32 mx-auto mb-4">
            <UserAvatar user={user} className="w-full h-full border-4 border-background shadow-lg" />
          </div>
          <CardTitle className="text-3xl font-bold text-foreground">{user.username}</CardTitle>
          <CardDescription className="text-lg text-muted-foreground">用户ID: {user.id}</CardDescription>
        </CardHeader>
        
        <CardContent className="p-6 space-y-8">
          <section>
            <h2 className="text-2xl font-semibold mb-4 flex items-center text-primary">
              <Ticket className="mr-3 h-6 w-6" />
              已加入的活动 ({joinedEvents.length})
            </h2>
            {joinedEvents.length > 0 ? (
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {joinedEvents.map(event => (
                  <EventCard key={event.id} event={event} />
                ))}
              </div>
            ) : (
              <Alert>
                <CalendarDays className="h-4 w-4" />
                <AlertTitle>此用户尚未加入任何活动</AlertTitle>
                <AlertDescription>
                  {isDebugView ? '此调试用户当前未加入任何活动。' : <span>浏览<Link href="/" className="text-primary hover:underline font-medium">活动列表</Link>并加入您感兴趣的活动吧！</span>}
                </AlertDescription>
              </Alert>
            )}
          </section>

          <Separator />

          <section>
            <h2 className="text-2xl font-semibold mb-4 flex items-center text-primary">
                <UserCircle className="mr-3 h-6 w-6" />
                账户信息
            </h2>
            <div className="space-y-3 text-muted-foreground">
                <p><strong>用户名:</strong> {user.username}</p>
                <p><strong>头像链接:</strong> <Link href={user.avatarUrl || '#'} target="_blank" rel="noopener noreferrer" className="text-accent hover:underline break-all">{user.avatarUrl || '未设置'}</Link></p>
            </div>
          </section>
        </CardContent>

        <CardFooter className="p-6 border-t">
            {isDebugView ? (
              <Button variant="outline" className="w-full" onClick={() => router.push('/login')}>
                <LogIn className="mr-2 h-4 w-4" />
                返回登录页
              </Button>
            ) : (
              <form action="/api/auth/logout" method="POST" className="w-full">
                  <Button type="submit" variant="destructive" className="w-full">
                      <LogOut className="mr-2 h-4 w-4" /> 退出登录
                  </Button>
              </form>
            )}
        </CardFooter>
      </Card>
    </div>
  );
}
